<template>
  <v-app class="grey lighten-4">
    <v-divider></v-divider>
    <v-card flat>
      <v-toolbar dense flat height="30px" color="white" class="mb-1">app.product-list</v-toolbar>
    </v-card>
    <v-toolbar-title class="mt-2">Product</v-toolbar-title>

    <div class="ma-3" style="border:1px solid lightgray;border-radius:3px">
      <v-toolbar flat color="white">
        <v-toolbar-title class="primary--text">Product</v-toolbar-title>
        <v-divider class="mx-2 primary" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <v-btn slot="activator" color="primary" dark class="mb-2 text-capitalize">
            <v-icon class="mr-2">add_circle</v-icon>Add
          </v-btn>
          <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
              <v-container grid-list-md>
                <v-layout wrap>
                  <v-flex xs12 sm12 md12>
                    <v-text-field v-model="editedItem.title" label="Title"/>
                  </v-flex>
                  <v-flex xs12 sm12 md12>
                    <v-textarea v-model="editedItem.description" label="Description"/>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
      <v-divider/>
      <v-card flat>
        <v-data-table
          id="printMe"
          :headers="headers"
          :items="items"
          :loading="loading"
          :rows-per-page-text="$t('roles_per_page')"
          :search="search"
        >
          <template slot="headerCell" slot-scope="props">
            <span class="table-header-text caption font-weight-bold">{{ $t(props.header.text) }}</span>
          </template>

          <template slot="items" slot-scope="props">
            <td>{{ props.index+1 }}</td>
            <td>{{ props.item.name }}</td>
            <td>{{ props.item.description }}</td>

            <td class="justify-center layout px-0">
              <v-menu offset-y>
                <v-btn slot="activator" small color="primary" dark class="text-capitalize">Action
                  <v-icon small>expand_more</v-icon>
                </v-btn>
                <v-list>
                  <v-list-tile
                    ripple
                    @click="$router.push({name:'role-update',params:{roleId:props.item.id}})"
                  >
                    <v-list-tile-title>{{ $t("action_edit") }}</v-list-tile-title>
                  </v-list-tile>
                  <v-list-tile @click="deleteItem(props.item.id)">
                    <v-list-tile-title>{{ $t("action_delete") }}</v-list-tile-title>
                  </v-list-tile>
                </v-list>
              </v-menu>
            </td>
          </template>
        </v-data-table>
      </v-card>
    </div>
  </v-app>
</template>


<script>
import { PushNotificationAPI } from "@/api";
import { tableMixin } from "@/shared/mixins";
export default {
  mixins: [tableMixin],
  data: () => ({
    resource: PushNotificationAPI,
    resourceName: "push notification",
    dialog: false,
    headers: [
      {
        text: "#",
        value: "id",
        sortable: false
      },
      {
        text: "Name",
        align: "left",
        value: "name"
      },
      {
        text: "Category",
        align: "left",
        value: "category"
      },
      {
        text: "Type",
        align: "left",
        value: "type"
      },
      {
        text: "Brand",
        align: "left",
        value: "brand"
      },
      {
        text: "Description",
        value: "description"
      },
      {
        text: "Action",
        value: "action",
        align: "center",
        sortable: false
      }
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      description: 0
    },
    defaultItem: {
      name: "",
      description: 1
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "New Push Notification"
        : "Edit Push Notification";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.desserts = [
        {
          name: "Title 1",
          description: "Some description"
        },
        {
          name: "Title 2",
          description: "Some description"
        },
        {
          name: "Title 3",
          description: "Some description"
        }
      ];
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.desserts.splice(index, 1);
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        PushNotificationAPI.create(this.editedItem);
        this.items.push(this.editedItem);
      }
      this.close();
    }
  }
};
</script>

