<template>
  <v-app class="grey lighten-4">
    <v-divider></v-divider>
    <v-card flat>
      <v-toolbar dense flat height="30px" color="white" class="mb-1">app.role-create</v-toolbar>
    </v-card>
    <v-toolbar-title class="mt-2">role</v-toolbar-title>
    <div class="ma-3" style="border:1px solid lightgray;border-radius:3px">
      <v-form @submit.prevent="save">
        <v-toolbar flat dense class="white">
          <v-tooltip bottom>
            <v-btn slot="activator" icon @click="$router.push({ name: 'role-list' })">
              <v-icon>arrow_back</v-icon>
            </v-btn>
            <span>{{ $t("go back") }}</span>
          </v-tooltip>
          <v-toolbar-title
            class="blue-grey--text text--darken-2 font-weight-bold"
          >{{ $t("create_role") }}</v-toolbar-title>
          <v-spacer/>
          <v-btn @click="$router.push({ name: 'role-list' })" class="text-capitalize">Cancel</v-btn>
          <v-btn color="primary white--text" @click="save" class="text-capitalize">Save</v-btn>
        </v-toolbar>
        <v-divider/>
        <v-card flat>
          <v-container>
            <form-error-messages :messages="errorMessage"/>
            <v-layout row justify-center pt-3>
              <v-flex md11>
                <!-- row 1 -->
                <v-layout row wrap>
                  <v-flex pr-3 xs12>
                    <v-text-field
                      v-validate="'required'"
                      autofocus
                      label="Role name"
                      v-model="item.name"
                      name="role name"
                      :error-messages="errors.collect('role name')"
                    />
                  </v-flex>
                  <v-flex pr-3 xs12>
                    <v-textarea label="Description" v-model="item.description"/>
                  </v-flex>
                </v-layout>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-form>
    </div>
  </v-app>
</template>

<script>
import { RoleAPI } from "@/api/";
export default {
  data() {
    return {
      item: {}
    };
  },
  methods: {
    async save() {
      const valid = await this.$validator.validateAll();
      if (valid) {
        this.errorMessage = null;
        try {
          await RoleAPI.create(this.item);
          this.$notify({
            type: "success",
            title: this.$t("success"),
            message: `${this.$t(this.resourceName)} ${this.$t(
              "created"
            )} ${this.$t("successfully")}.`
          });
          location.reload(true);
          this.$router.push({ name: "role-list" });
        } catch (err) {
          if (err.statusCode === 422) {
            const { messages } = err.details;
            this.errorMessage = messages;
          }
        }
      }
    }
  }
};
</script>

