import {
  Login,
  ForgotPassword
} from '@/modules/account';
import NotFoundPage from '@/shared/components/NotFoundPage.vue';
import AppLayout from '@/modules/layout';
import Dashboard from '@/modules/dashboard';
import userAccountRoutes from '@/modules/account/account.routes';
import roleRoutes from '@/modules/role/role.routes'
import pushNotificationList from '@/modules/pushNotification/push-notofication.routes'
import permissionRoutes from '@/modules/permission/permission.routes'

import coinRoutes from '@/modules/coin/coin.routes'
import productRoutes from '@/modules/product/product.routes'

const routes = [{
    path: '/',
    redirect: '/app',
  },
  {
    path: '/app',
    component: AppLayout,
    redirect: '/app/dashboard',
    children: [{
        path: 'dashboard',
        name: 'dashboard',
        component: Dashboard,
      },

      ...permissionRoutes,
      ...roleRoutes,
      ...pushNotificationList,
      ...userAccountRoutes,
      ...coinRoutes,
      ...productRoutes
    ],
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/forgot',
    name: 'forgot',
    component: ForgotPassword,
  },
  {
    path: '*',
    name: 'not-found',
    component: NotFoundPage,
  },
];

export default routes;