<template>
  <v-app class="grey lighten-4">
    <v-divider/>
    <v-card flat>
      <v-toolbar dense flat height="30px" color="white" class="mb-1">app.push-notification</v-toolbar>
    </v-card>
    <v-toolbar-title class="mt-2">Push Notification</v-toolbar-title>

    <div class="ma-3" style="border:1px solid lightgray;border-radius:3px">
      <v-card flat>
        <v-container>
          <v-layout row wrap justify-center>
            <v-flex xs11>
              <v-card flat>
                <v-card-text>
                  <div>
                    <v-toolbar flat color="white">
                      <v-toolbar-title>Zoma Shop</v-toolbar-title>
                      <v-divider class="mx-2 primary" inset vertical></v-divider>
                      <v-spacer></v-spacer>
                      <v-dialog v-model="dialog" max-width="500px">
                        <v-btn slot="activator" color="primary" dark class="mb-2 text-capitalize">
                          <v-icon class="mr-2">add_circle</v-icon>Add
                        </v-btn>
                        <v-card>
                          <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                          </v-card-title>
                          <v-card-text>
                            <v-container grid-list-md>
                              <v-layout wrap>
                                <v-flex xs12 sm12 md12>
                                  <v-text-field v-model="editedItem.title" label="Title"/>
                                </v-flex>
                                <v-flex xs12 sm12 md12>
                                  <v-textarea v-model="editedItem.description" label="Description"/>
                                </v-flex>
                              </v-layout>
                            </v-container>
                          </v-card-text>

                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" flat @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" flat @click="save">Save</v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                    </v-toolbar>
                    <v-data-table :headers="headers" :items="items" class="elevation-1">
                      <template slot="items" slot-scope="props">
                        <td>{{props.index+1}}</td>
                        <td>{{ props.item.name }}</td>
                        <td>{{ props.item.description }}</td>
                        <td class="justify-center layout px-0">
                          <v-icon small class="mr-2" @click="editItem(props.item)">edit</v-icon>
                          <v-icon small @click="deleteItem(props.item)">delete</v-icon>
                        </td>
                      </template>
                    </v-data-table>
                  </div>
                </v-card-text>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </div>
  </v-app>
</template>

<script>
import { PushNotificationAPI } from "@/api";
import { tableMixin } from "@/shared/mixins";
export default {
  mixins: [tableMixin],
  data: () => ({
    resource: PushNotificationAPI,
    resourceName: "push notification",
    dialog: false,
    headers: [
      {
        text: "#",
        value: "id",
        sortable: false
      },
      {
        text: "Title",
        align: "left",
        value: "title"
      },
      {
        text: "Description",
        value: "description"
      },
      {
        text: "Action",
        value: "action",
        align: "center",
        sortable: false
      }
    ],
    desserts: [],
    editedIndex: -1,
    editedItem: {
      name: "",
      description: 0
    },
    defaultItem: {
      name: "",
      description: 1
    }
  }),

  computed: {
    formTitle() {
      return this.editedIndex === -1
        ? "New Push Notification"
        : "Edit Push Notification";
    }
  },

  watch: {
    dialog(val) {
      val || this.close();
    }
  },

  created() {
    this.initialize();
  },

  methods: {
    initialize() {
      this.desserts = [
        {
          name: "Title 1",
          description: "Some description"
        },
        {
          name: "Title 2",
          description: "Some description"
        },
        {
          name: "Title 3",
          description: "Some description"
        }
      ];
    },

    editItem(item) {
      this.editedIndex = this.desserts.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      const index = this.desserts.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.desserts.splice(index, 1);
    },

    close() {
      this.dialog = false;
      setTimeout(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      }, 300);
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.desserts[this.editedIndex], this.editedItem);
      } else {
        PushNotificationAPI.create(this.editedItem);
        this.items.push(this.editedItem);
      }
      this.close();
    }
  }
};
</script>

