<template>
  <div>
    <v-toolbar
      dark
      dense
      color="primary"
      app
      :fixed="toolbar.fixed"
      :clipped-left="toolbar.clippedLeft"
      clipped-right
    >
      <v-toolbar-side-icon @click="toggleDrawer"></v-toolbar-side-icon>
      <v-avatar size="40px" color="white">
        <h1 class="primary--text">
          <strong>Z</strong>
        </h1>
      </v-avatar>
      <h3 class="mx-2">
        <strong class="red--text">Zoma</strong>
        <strong class="mx-2">Shop</strong>
      </h3>
      <v-spacer/>
      <v-text-field
        v-model="search"
        name="name"
        label="Search"
        id="id"
        solo-inverted
        dark
        prepend-icon="search"
        height="10"
      />

      <v-btn icon>
        <v-icon>apps</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>refresh</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>notifications</v-icon>
      </v-btn>

      <v-toolbar-items class="hidden-sm-and-down">
        <v-menu :close-on-content-click="false" :nudge-width="150" offset-x>
          <v-btn flat slot="activator">
            <v-icon>more_vert</v-icon>
          </v-btn>
          <v-card>
            <v-list>
              <v-list-tile @click="goToUserProfilePage()">
                <v-list-tile-avatar>
                  <img src="@/assets/avatar_anonymous.png" alt="Avator">
                </v-list-tile-avatar>
                <v-list-tile-content>
                  <v-list-tile-title>{{ user.fullName }}</v-list-tile-title>
                  <v-list-tile-sub-title>{{ user.role }}</v-list-tile-sub-title>
                </v-list-tile-content>
                <v-list-tile-action/>
              </v-list-tile>
              <v-divider/>
              <v-list-tile>
                <v-list-tile-action>
                  <v-switch
                    label=""
                    :input-value="currentLanguage === 'am'"
                    color="primary"
                    @change="toggleLanguage()"
                  />
                </v-list-tile-action>
              </v-list-tile>
              <v-list-tile>
                <v-list-tile-action>
                  <v-switch
                    :label="$t('dark_theme')"
                    :input-value="darkTheme"
                    color="black"
                    @change="toggleDarkTheme()"
                  />
                </v-list-tile-action>
              </v-list-tile>
            </v-list>
            <v-card-actions>
              <v-btn block color="primary" @click="logout">{{ $t("log_out") }}</v-btn>
            </v-card-actions>
          </v-card>
        </v-menu>
      </v-toolbar-items>
    </v-toolbar>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import AccountService from "@/shared/services";
import { eventBus } from "@/main.js";

export default {
  name: "NavBar",
  data() {
    return {
      toolbar: {
        fixed: true,
        clippedLeft: true
      },
      user: AccountService.getProfile(),
      profilePopupVisible: false
    };
  },
  computed: {
    ...mapState("theme", ["darkTheme"]),
    ...mapState("core", ["currentLanguage", "appName"])
  },
  methods: {
    toggleDrawer() {
      eventBus.$emit("toggleDrawer", "data");
    },
    goToUserProfilePage() {
      this.profilePopupVisible = false;
      this.$router.push({
        name: "user-profile"
      });
    },
    ...mapMutations("theme", ["toggleDarkTheme"]),
    // ...mapMutations("layout", ["toggleDrawer"]),
    ...mapMutations("core", ["toggleLanguage"]),

    logout() {
      AccountService.logout();
    }
  }
};
</script>






