<template>
  <v-conatiner>
    <v-navigation-drawer
      width="250px"
      :clipped="drawer.clipped"
      :fixed="drawer.fixed"
      :permanent="drawer.permanent"
      :mobile-break-point="3000"
      :mini-variant="drawer.mini"
      v-model="drawer.open"
      stateless
      hide-overlay
      app
      :style="cssProps"
    >
      <v-divider/>
      <v-toolbar color="white" flat>
        <v-list>
          <v-list-tile avatar>
            <v-list-tile-action>
              <v-avatar size="40px" color="primary">
                <h1 class="white--text">
                  <strong>Z</strong>
                </h1>
              </v-avatar>
            </v-list-tile-action>
            <v-list-tile-title>
              <h3>
                <strong class="primary--text">Zoma</strong>
                <strong class="mx-2">Shop</strong>
              </h3>
            </v-list-tile-title>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-divider></v-divider>

      <v-list>
        <template v-for="menu in menus">
          <v-list-tile v-if="!menu.children" :key="menu.title" :to="{ name: menu.route }" ripple>
            <v-list-tile-action>
              <v-icon>{{ menu.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-title>{{ $t(menu.title) }}</v-list-tile-title>
          </v-list-tile>
          <v-list-group
            v-if="menu.children"
            :key="menu.title"
            :prepend-icon="menu.icon"
            :value="menuGroupShouldBeExpanded(menu)"
          >
            <v-list-tile slot="activator">
              <v-list-tile-title>{{ $t(menu.title) }}</v-list-tile-title>
            </v-list-tile>

            <v-list-tile
              v-for="submenu in menu.children"
              :key="submenu.title"
              :to="{name:submenu.route}"
            >
              <v-list-tile-action>
                <v-icon>{{ submenu.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>{{ $t(submenu.title) }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list-group>
        </template>
      </v-list>

      <div class="text-xs-right">
        <v-btn
          icon
          v-if="drawer.mini"
          @click="drawer.mini = !drawer.mini;"
          fixed
          bottom
          right
          class="mb-3"
        >
          <v-icon>keyboard_arrow_right</v-icon>
        </v-btn>
        <v-btn
          v-if="!drawer.mini"
          flat
          @click="drawer.mini = !drawer.mini;"
          fixed
          bottom
          block
          class="mb-3 text-capitalize"
        >
          <v-icon>keyboard_arrow_left</v-icon>
          <span>Collapse</span>
        </v-btn>
      </div>
    </v-navigation-drawer>
  </v-conatiner>
</template>

<script>
import { mapState } from "vuex";
import { eventBus } from "@/main.js";

export default {
  data: () => ({
    mini: false,
    right: null,
    menus: [
      {
        title: "Home",
        icon: "dashboard",
        route: "dashboard",
        allowedRoles: ["admin", "super-admin"]
      },
      {
        title: "Products",
        icon: "notifications",

        children: [
           {
            title: "Electronics",
            route: "electronics-list"
          },
          {
            title: "Men's",
            route: "product-list"
          },
          {
            title: "Female's",
            route: "product-list"
          },
          {
            title: "News",
            route: "product-list"
          },
         
        ]
      },
      {
        title: "Coin",
        icon: "check_box",

        allowedRoles: ["admin", "super-admin"],
        children: [
          {
            title: "List Accounts",
            route: "coin-list"
          },
          {
            title: "Fill Coin",
            route: "coin-fill"
          },
          {
            title: "Transfer Coin",
            route: "coin-transfer"
          }
        ]
      },
      {
        title: "Push Notification",
        icon: "notifications",
        route: "pushNotification-list"
      },

      {
        title: "Role",
        icon: "group",
        route: "role-list",
        allowedRoles: ["admin", "super-admin"]
      },
      {
        title: "Users",
        icon: "account_circle",
        route: "user-list",
        allowedRoles: ["admin", "super-admin"]
      }
    ],
    drawer: {
      open: null,
      clipped: true,
      fixed: false,
      permanent: true,
      mini: false
    },
    footer: {
      fixed: true,
      clippedLeft: true
    }
  }),

  computed: {
    ...mapState("core", ["appName"]),
    cssProps() {
      return {
        "--secondary-color": this.$vuetify.theme.secondary
      };
    },
    drawerStatus: {
      get() {
        return this.$store.state.layout.navDrawerToggle;
      },
      set(newValue) {
        this.$store.state.layout.navDrawerToggle = newValue;
      }
    }
  },
  created() {
    eventBus.$on("toggleDrawer", data => {
      this.toggleDrawer();
    });
  },
  methods: {
    menuGroupShouldBeExpanded(menu) {
      if (menu.childrenUrlMatcher) {
        return this.$route.fullPath.includes(menu.childrenUrlMatcher);
      }

      return false;
    },
    tgl() {
      this.toggleDrawer();
    },
    makeDrawerPermanent() {
      this.drawer.permanent = true;
      this.drawer.clipped = false;
    },
    toggleDrawer() {
      if (this.drawer.permanent) {
        this.drawer.permanent = !this.drawer.permanent;
        this.drawer.clipped = true;
        this.drawer.open = !this.drawer.open;
      } else {
        this.drawer.open = !this.drawer.open;
      }
    }
  }
};
</script>

