<template>
  <v-app class>
    <v-container grid-list-xs>
      <v-layout row wrap justify-center>
        <v-flex xs12 sm12 md5 lg5>
          <v-layout row wrap justify-center>
            <v-flex xs12 sm6 md5 lg8>
              <v-card style="border-radius:6px" class="white">
                <v-toolbar flat dense dark color="teal darken-3">
                  <v-icon>person</v-icon>
                  <v-toolbar-title>Login</v-toolbar-title>
                </v-toolbar>
                <v-card-text>
                  <v-container>
                    <v-form @submit.prevent="login">
                      <v-alert v-if="authError" :value="true" type="error">{{ authError }}</v-alert>
                      <v-text-field
                        v-validate="'required'"
                        v-model="user.email"
                        :error-messages="errors.collect('email')"
                        :label="$t('email')"
                        prepend-icon="person"
                        name="email"
                        type="text"
                        autocomplete="email"
                      />
                      <v-text-field
                        v-validate="{ required: true, min: 5 }"
                        v-model="user.password"
                        :error-messages="errors.collect('password')"
                        :label="$t('password')"
                        prepend-icon="lock"
                        name="password"
                        autocomplete="current-password"
                        type="password"
                      />
                      <div class="ml-3">
                        <v-checkbox label="remember" color="teal" v-model="value" value="value"/>
                      </div>
                      <div class="text-xs-center my-1">
                        <v-btn
                          :loading="loading"
                          type="submit"
                          block
                          color="teal"
                          dark
                          class="text-capitalize"
                        >{{ $t("log_in") }}</v-btn>
                      </div>
                    </v-form>
                  </v-container>
                </v-card-text>
                <v-toolbar dense color="teal darken-4" class="text-xs-right">
                  <router-link :to="{ name: 'forgot' }" class="white--text forgotPassword">
                    <span class="mx-3">{{ $t("forgot_password") }}</span>
                    <v-icon small color="white">arrow_forward</v-icon>
                  </router-link>
                </v-toolbar>
              </v-card>
            </v-flex>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-container>
  </v-app>
</template>


<script>
import AccountService from "@/shared/services";

export default {
  name: "Login",
  data() {
    return {
      loading: false,
      authError: false,
      user: {}
    };
  },
  methods: {
    login() {
      const self = this;
      this.$validator.validateAll().then(valid => {
        if (valid) {
          this.loading = true;
          self.authError = null;
          AccountService.login(this.user.email, this.user.password)
            .then(() => {
              this.$validator.reset();
              this.$router.push({ name: "dashboard" });
            })
            .catch(err => {
              this.loading = false;
              if (err.statusCode === 401) {
                self.authError = "Username or password is incorrect";
              } else if (err.statusCode === 403) {
                self.authError = "Unauthorized to log in.";
              }
            });
        }
      });
    }
  }
};
</script>

<style>
.forgotPassword {
  text-decoration: none;
}
</style>

